@import url(https://fonts.googleapis.com/css?family=Montserrat);
.popup {
    position:fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 1200vh;
    background-color:rgba(0, 0, 0, 0.2);
    display:-ms-grid;
    justify-content: center;
    align-items: center;
    overflow-y:scroll;
    max-height:100%;
}

.popup-inner {
    position: relative;
    padding:32px;
    width: 100%;
    max-width: 800px;
    background-color:#FFF;
    overflow-y:scroll; 
    max-height:100%;

}

.popup-inner .close-btn {
    position: absolute;
    top: 100px;
    right: 25px;
}

.popup-inner .close {
    position: absolute;
    top: 80px;
    right: 25px;
}


